/* image edit */

.myimage img{
    height:200px;
    width:200px;
    object-fit: cover;
    object-position:top;
    border-radius: 50%;
	transition: all 2s linear;
	box-shadow: 0 0 0 0 rgb(0, 0, 0);
	-webkit-transform: scale(1);
	        transform: scale(1);
	-webkit-animation: pulse 3s infinite;
	        animation: pulse 3s infinite;
}

@-webkit-keyframes pulse {
	0% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 var(--dark-black);
	}

	70% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		box-shadow: 0 0 0 var(--len) var(--light-black);
	}

	100% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 var(--light-black);
	}
}

@keyframes pulse {
	0% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 var(--dark-black);
	}

	70% {
		-webkit-transform: scale(1);
		        transform: scale(1);
		box-shadow: 0 0 0 var(--len) var(--light-black);
	}

	100% {
		-webkit-transform: scale(0.95);
		        transform: scale(0.95);
		box-shadow: 0 0 0 0 var(--light-black);
	}
}

