.nav-toggle{
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    z-index: 99;
    padding: 15px 0 5px 5px;
    position: absolute !important; 
    
  }
  
  .switch {
      position: relative;
      display: inline-block;
      width: 60px;
      height: 34px;
    }
    
    .switch input {
      opacity: 0;
      width: 0;
      height: 0;
    }
    
    /* The slider */
    .slider {
      position: absolute;
      cursor: pointer;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-color: #ccc;
      transition: .4s;
    }
    
    .slider:before {
      position: absolute;
      content: "";
      height: 26px;
      width: 26px;
      left: 4px;
      bottom: 4px;
      background-color: black;
      transition: .4s;
    }
    
    input:checked + .slider {
      background-color: white;
    }
    
    input:focus + .slider {
      box-shadow: 0 0 1px black;
    }
    
    input:checked + .slider:before {
      -webkit-transform: translateX(26px);
      transform: translateX(26px);
    }
    
    .slider.round {
      border-radius: 34px;
    }
    
    .slider.round:before {
      border-radius: 50%;
    }
