:root {
  --light-black: rgba(0, 0, 0, 0);
  --dark-black:rgba(0, 0, 0, 0.7);

  --len:10px;

  --backgroundhobby:#E4D8DC;

  --lightdivider1:#ececec;
  --lightdivider2:#FDF5E6;
  --lightdivider3:#CEE5D0;
  --lightdivider4:#E4D8DC;
  --lightdivider5:#DEEDF0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
